import React, { useState, useEffect } from 'react';
import './Profile.css';
import axios from 'axios';
import { UNSAFE_useScrollRestoration } from 'react-router';


//fetch('http://localhost:5292/phonePage/2').then(response => response.json()).then(data => console.log(data)) //ID-T KISZEDNI A / MGL HA VAN


const Profile = () => {
  // Felhasznl adatok

  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [userData, setUserData] = useState({ userFullName: '', userEmail: '' });
  const [userID, setUserID] = useState(null);
  const savedEmail = localStorage.getItem('email');
  
  // UserID betltse az email alapjn
  useEffect(() => {
    const loadUserData = async () => {
      try {
        const savedName = localStorage.getItem('fullname');
        setUserData({ userFullName: savedName, userEmail: savedEmail });

        if (savedEmail) {
          const response = await axios.get(`http://localhost:5175/api/Registration/GetId/${encodeURIComponent(savedEmail)}`);
          if (response.data) {
            setUserID(response.data);
            localStorage.setItem('userid', response.data);
          }
        }
      } catch (error) {
        console.error('Hiba a user adatok betltsekor:', error);
      }
    };

    loadUserData();
  }, [savedEmail]);

  // Cmek betltse az API-bl
  useEffect(() => {
    const loadAddresses = async () => {
      try {
        if (!userID) {
          console.error('UserID nem elrhet');
          return;
        }

        // Szlltsi cmek (addressType=1)
        const shippingRes = await axios.get(`http://localhost:5175/api/address/GetAddresses/${userID}/1`);
        if (Array.isArray(shippingRes.data)) {
          setShippingAddresses(shippingRes.data);
        }

        // Szmlzsi cmek (addressType=0)
        const billingRes = await axios.get(`http://localhost:5175/api/address/GetAddresses/${userID}/0`);
        if (Array.isArray(billingRes.data)) {
          setBillingAddresses(billingRes.data);
        }
      } catch (error) {
        console.error('Hiba a cmek betltsekor:', error);
      }
    };

    loadAddresses();
  }, [userID]);

  const [passwordStrength, setPasswordStrength] = useState({ score: 0, label: "", color: "#ccc" });
  const [statusMessage, setStatusMessage] = useState({ text: "", isError: false });
  const [showSuccessModal, setShowSuccessModal] = useState(false);
  const [addressStatusMessage, setAddressStatusMessage] = useState({ text: "", isError: false });

  const [phones, setPhones] = useState([]);
  const [serviceRequests, setServiceRequests] = useState([]);

  // Szervzrendelsek betltse az API-bl
  useEffect(() => {
    const loadServiceRequests = async () => {
      try {
        if (!userID) {
          console.error('UserID nem elrhet');
          return;
        }

        const response = await axios.get(`http://localhost:5175/api/Profile/GetRepair/${userID}`);
        if (Array.isArray(response.data)) {
          setServiceRequests(response.data);
        }
      } catch (error) {
        console.error('Hiba a szervzrendelsek betltsekor:', error);
      }
    };

    loadServiceRequests();
  }, [userID]);

  // Rendelsek betltse az API-bl
  useEffect(() => {
    const loadOrders = async () => {
      try {
        if (!userID) {
          console.error('UserID nem elrhet');
          return;
        }

        const response = await axios.get(`http://localhost:5175/api/Profile/GetOrder/${userID}`);
        if (Array.isArray(response.data)) {
          setPhones(response.data);
        }
      } catch (error) {
        console.error('Hiba a rendelsek betltsekor:', error);
      }
    };

    loadOrders();
  }, [userID]);

  // Sttusz konvertlsa szvegre
  const getStatusText = (statusCode) => {
    switch (statusCode) {
      case 0:
        return 'feldolgozs alatt';
      case 1:
        return 'feldolgozva';
      case 2:
        return 'tvtelre ksz';
      case 3:
        return 'teljestve';
      default:
        return 'ismeretlen';
    }
  };

  // Szlltsi cmek
  const [shippingAddresses, setShippingAddresses] = useState([]);
  
  // Szmlzsi cmek
  const [billingAddresses, setBillingAddresses] = useState([]);

  // j cm form
  const [newAddress, setNewAddress] = useState({
    type: 'shipping',
    postalCode: '',
    city: '',
    address: '',
    phoneNumber: ''
  });

  // Mdosts sttuszok
  const [editingProfile, setEditingProfile] = useState(false);
  const [editingShippingId, setEditingShippingId] = useState(null);
  const [editingBillingId, setEditingBillingId] = useState(null);
  const [showNewAddressForm, setShowNewAddressForm] = useState(false);
  const [newAddressType, setNewAddressType] = useState(null); // 'shipping' vagy 'billing'

  // Delete modal state
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [deleteAddressType, setDeleteAddressType] = useState(null);
  const [deleteAddressId, setDeleteAddressId] = useState(null);

  // function GetProfile(){
  //   fetch("")
  // }

  // Profil adatok mentse
  const handleProfileSubmit = (e) => {
    e.preventDefault();
    setShowConfirmModal(true);
  };

  const confirmSave = async () => {
    try {
      console.log(userData)
      const response = await axios.put(
        `http://localhost:5175/api/Profile/UpdateUser/${savedEmail}`,
        {
          userFullName: userData.userFullName,
          userEmail: userData.userEmail
        }
      );

      if (response.status === 200) {
        // Itt is figyelj a konzisztens elnevezsre:
        localStorage.setItem('fullname', userData.userFullName);
        localStorage.setItem('email', userData.userEmail);
        window.dispatchEvent(new Event('userProfileUpdated'));

        setEditingProfile(false);
        setShowConfirmModal(false);
        setShowSuccessModal(true);
        setTimeout(() => {
          setShowSuccessModal(false);
        }, 2000);
      }
    } catch (error) {
      // Axios hiba esetn itt nzd meg, mit mond a szerver:
      console.error('Hiba:', error.response?.data || error.message);
      alert('Hiba trtnt a ments sorn!');
    }
  };

  //Salt generator
  function GenerateSalt(SaltLength) {
    const karakterek = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    let salt = "";

    for (let i = 0; i < SaltLength; i++) {
      const randomIndex = Math.floor(Math.random() * karakterek.length);
      salt += karakterek[randomIndex];
    }

    return salt;
  }

  const checkPasswordStrength = (value) => {
    let score = 0;
    if (value.length >= 8) score++;
    if (/[A-Z]/.test(value)) score++;
    if (/[a-z]/.test(value)) score++;
    if (/[0-9]/.test(value)) score++;
    if (/[^A-Za-z0-9]/.test(value)) score++;

    if (!value) {
      setPasswordStrength({ score: 0, label: "", color: "#ccc" });
    } else if (score <= 2) {
      setPasswordStrength({ score, label: "Gyenge", color: "red" });
    } else if (score <= 4) {
      setPasswordStrength({ score, label: "Kzepes", color: "orange" });
    } else {
      setPasswordStrength({ score, label: "Ers", color: "green" });
    }
  };

  // Jelsz vltoztats
  const handlePasswordChange = async (e) => {
    e.preventDefault();
    setStatusMessage({ text: "", isError: false }); // Elz zenet trlse

    if (userData.newPassword !== userData.confirmPassword) {
      // alert helyett:
      setStatusMessage({ text: "Az j jelsz s a megersts nem egyezik!", isError: true });
      return;
    }

    if (passwordStrength.score < 5) {
      // alert helyett:
      setStatusMessage({ text: "A jelsz nem elg ers! Hasznljon kisbett, nagybett, szmot s specilis karaktert!", isError: true });
      return;
    }

    try {
      const saltResponse = await axios.get(`http://localhost:5175/api/Login/GetSalt/${encodeURIComponent(userData.userEmail)}`);
      if (saltResponse.status !== 200) throw new Error("Nem sikerlt lekrni a saltot.");
      const currentDbSalt = await saltResponse.data;

      const oldHashed = await hashPassword(userData.currentPassword, currentDbSalt);

      const newSalt = GenerateSalt(64);
      const newHashed = await hashPassword(userData.newPassword, newSalt);

      const response = await axios.put("http://localhost:5175/api/Registration/ChangePassword", {
        email: userData.userEmail,
        oldPassword: oldHashed,
        newPassword: newHashed,
        salt: newSalt
      });

      if (response.status === 200) {
        setStatusMessage({ text: "Sikeres jelsz mdosts!", isError: false });

        setUserData(prev => ({
          ...prev,
          currentPassword: "",
          newPassword: "",
          confirmPassword: ""
          
        }));
        
        setPasswordStrength({ score: 0, label: "", color: "#ccc" });
      } else {
        const errorMsg = await response.data;
        setStatusMessage({ text: "Hiba: " + errorMsg, isError: true });
      }
    } catch (error) {
      console.error("Hiba trtnt:", error);
      setStatusMessage({ text: "Hlzati hiba trtnt!", isError: true });
    }
  };

  const hashPassword = async (password, salt) => {
    const combinedPassword = password + salt;
    const msgBuffer = new TextEncoder().encode(combinedPassword);
    const hashBuffer = await crypto.subtle.digest('SHA-256', msgBuffer);
    const hashArray = Array.from(new Uint8Array(hashBuffer));
    return hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
  };


  // Szlltsi cm mentse
  const handleShippingSubmit = async (e) => {
    e.preventDefault();
    setAddressStatusMessage({ text: "", isError: false });

    try {
      if (shippingAddresses.length === 0) return;

      if (!userID) {
        setAddressStatusMessage({ text: "Bejelentkezs szksges!", isError: true });
        return;
      }

      const address = shippingAddresses[0];

      const response = await axios.put(
        `http://localhost:5175/api/address/PutAddress/${userID}/1`,
        {
          postalCode: address.postalCode,
          city: address.city,
          address: address.address,
          phoneNumber: address.phoneNumber,
          addressType: 1,
          userID: parseInt(userID)
        }
      );

      if (response.status === 200) {
        setAddressStatusMessage({ text: "Szlltsi cm sikeresen frisstve!", isError: false });
        setEditingShippingId(null);
        setTimeout(() => setAddressStatusMessage({ text: "", isError: false }), 3000);
      }
    } catch (error) {
      const errorMsg = error.response?.data || 'Hiba trtnt a ments sorn!';
      setAddressStatusMessage({ text: errorMsg, isError: true });
    }
  };

  // Szmlzsi cm mentse
  const handleBillingSubmit = async (e) => {
    e.preventDefault();
    setAddressStatusMessage({ text: "", isError: false });

    try {
      if (billingAddresses.length === 0) return;

      if (!userID) {
        setAddressStatusMessage({ text: "Bejelentkezs szksges!", isError: true });
        return;
      }

      const address = billingAddresses[0];

      const response = await axios.put(
        `http://localhost:5175/api/address/PutAddress/${userID}/0`,
        {
          postalCode: address.postalCode,
          city: address.city,
          address: address.address,
          phoneNumber: address.phoneNumber,
          addressType: 0,
          userID: parseInt(userID)
        }
      );

      if (response.status === 200) {
        setAddressStatusMessage({ text: "Szmlzsi cm sikeresen frisstve!", isError: false });
        setEditingBillingId(null);
        setTimeout(() => setAddressStatusMessage({ text: "", isError: false }), 3000);
      }
    } catch (error) {
      const errorMsg = error.response?.data || 'Hiba trtnt a ments sorn!';
      setAddressStatusMessage({ text: errorMsg, isError: true });
    }
  };

  // j cm hozzadsa
  const handleAddNewAddress = async (e) => {
    e.preventDefault();
    setAddressStatusMessage({ text: "", isError: false });

    try {
      if (!userID) {
        setAddressStatusMessage({ text: "Bejelentkezs szksges!", isError: true });
        return;
      }

      const addressType = newAddress.type === 'shipping' ? 1 : 0;

      const response = await axios.post(
        `http://localhost:5175/api/address/PostAddress`,
        {
          postalCode: newAddress.postalCode,
          city: newAddress.city,
          address: newAddress.address,
          phoneNumber: newAddress.phoneNumber,
          addressType: addressType,
          userID: parseInt(userID)
        }
      );

      if (response.status === 200) {
        setAddressStatusMessage({ text: "j cm sikeresen hozzadva!", isError: false });
        
        // jra betltjk a cmeket
        setTimeout(async () => {
          const shippingRes = await axios.get(`http://localhost:5175/api/address/GetAddresses/${userID}/1`);
          if (Array.isArray(shippingRes.data)) {
            setShippingAddresses(shippingRes.data);
          }

          const billingRes = await axios.get(`http://localhost:5175/api/address/GetAddresses/${userID}/0`);
          if (Array.isArray(billingRes.data)) {
            setBillingAddresses(billingRes.data);
          }

          setAddressStatusMessage({ text: "", isError: false });
        }, 1500);

        // Form reset
        setNewAddress({
          type: 'shipping',
          postalCode: '',
          city: '',
          address: '',
          phoneNumber: ''
        });
        setNewAddressType(null);
      }
    } catch (error) {
      const errorMsg = error.response?.data || 'Hiba trtnt a cm hozzadsakor!';
      setAddressStatusMessage({ text: errorMsg, isError: true });
    }
  };

  // Cm trlsnek megerstse
  const deleteAddress = (type, id) => {
    setDeleteAddressType(type);
    setDeleteAddressId(id);
    setShowDeleteModal(true);
  };

  // Vals trls az API-n keresztl
  const confirmDeleteAddress = async () => {
    setAddressStatusMessage({ text: "", isError: false });

    try {
      if (!userID) {
        setAddressStatusMessage({ text: "Bejelentkezs szksges!", isError: true });
        return;
      }

      const addressType = deleteAddressType === 'shipping' ? 1 : 0;

      const response = await axios.delete(
        `http://localhost:5175/api/address/DeleteAddress/${userID}/${addressType}`
      );

      if (response.status === 200) {
        setAddressStatusMessage({ text: "Cm sikeresen trlve!", isError: false });

        // jra betltjk a cmeket
        setTimeout(async () => {
          const shippingRes = await axios.get(`http://localhost:5175/api/address/GetAddresses/${userID}/1`);
          if (Array.isArray(shippingRes.data)) {
            setShippingAddresses(shippingRes.data);
          }

          const billingRes = await axios.get(`http://localhost:5175/api/address/GetAddresses/${userID}/0`);
          if (Array.isArray(billingRes.data)) {
            setBillingAddresses(billingRes.data);
          }

          setAddressStatusMessage({ text: "", isError: false });
        }, 1500);
      }

      setShowDeleteModal(false);
      setDeleteAddressType(null);
      setDeleteAddressId(null);
    } catch (error) {
      const errorMsg = error.response?.data || 'Hiba trtnt a trls sorn!';
      setAddressStatusMessage({ text: errorMsg, isError: true });
      setShowDeleteModal(false);
    }
  };

  // Input vltozsok kezelse
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setUserData({ ...userData, [name]: value });

    if (name === "newPassword") {
      checkPasswordStrength(value);
    }
  };

  const handleAddressInputChange = (e, type, idx, field) => {
    const { value } = e.target;

    if (type === 'shipping') {
      const updated = shippingAddresses.map((addr, i) =>
        i === idx ? { ...addr, [field]: value } : addr
      );
      setShippingAddresses(updated);
    } else {
      const updated = billingAddresses.map((addr, i) =>
        i === idx ? { ...addr, [field]: value } : addr
      );
      setBillingAddresses(updated);
    }
  };

  const handleNewAddressChange = (e) => {
    const { name, value } = e.target;
    setNewAddress({ ...newAddress, [name]: value });
  };

  const passwordsDoNotMatch =
    !!userData.newPassword &&
    !!userData.confirmPassword &&
    userData.newPassword !== userData.confirmPassword;

  return (
    <div className="profile-container">
      {/* Fejlc */}
      <header className="profile-header">
        <h1>Profilom</h1>
        <p>Itt kezelheted szemlyes adataidat s cmeidet</p>
      </header>

      <div className="profile-content">
        {/* Bal oldali navigci */}
        <div className="profile-sidebar">
          <ul>
            <li><a href="#profile">Profil adatok</a></li>
            <li><a href="#shipping">Szlltsi cmek</a></li>
            <li><a href="#billing">Szmlzsi cmek</a></li>
            <li><a href="#orders">Rendelseim</a></li>
            <li><a href="#service-requests">Szerviz krsek</a></li>
          </ul>
        </div>

        {/* F tartalom */}
        <main className="profile-main">
          {/* 1. Profil adatok szakasz */}
          <section id="profile" className="profile-section">
            <div className="section-header">
              <h2>Profil adatok</h2>
              {!editingProfile ? (
                <button className="btn-edit" onClick={() => setEditingProfile(true)}>
                  Szerkeszts
                </button>
              ) : (
                <button className="btn-cancel" onClick={() => setEditingProfile(false)}>
                  Mgse
                </button>
              )}
            </div>

            {/* Profil szekci - az ltalad krt sszevont mezkkel */}
            <form onSubmit={handleProfileSubmit}>
              <div className="form-stack">
                <div className="form-group">
                  <label>Nv</label>
                  <input
                    type="text"
                    value={userData.userFullName}
                    onChange={(e) => setUserData({ ...userData, userFullName: e.target.value })}
                    disabled={!editingProfile}
                  />
                </div>
                <div className="form-group">
                  <label>Email cm</label>
                  <input
                    type="email"
                    value={userData.userEmail}
                    onChange={(e) => setUserData({ ...userData, userEmail: e.target.value })}
                    disabled={!editingProfile}
                  />
                </div>
              </div>
              {editingProfile && <button type="submit" className="btn-save">Ments</button>}
            </form>

            {showConfirmModal && (
              <div className="profile-modal-overlay">
                <div className="profile-modal-content">
                  <div className="profile-modal-header">
                    <h3 className="profile-modal-title">Megersts</h3>
                  </div>
                  <div className="profile-modal-body">
                    <p className="profile-modal-text">Biztosan menteni szeretnd a mdostsokat?</p>
                  </div>
                  <div className="profile-modal-actions">
                    <button onClick={() => setShowConfirmModal(false)} className="profile-modal-btn-cancel">
                      Mgse
                    </button>
                    <button onClick={confirmSave} className="profile-modal-btn-confirm">
                      Igen, ments
                    </button>
                  </div>
                </div>
              </div>
            )}

            {showSuccessModal && (
              <div className="profile-modal-overlay">
                <div className="profile-modal-content">
                  <div className="profile-modal-body">
                    <h3>Sikeres ments</h3>
                  </div>
                </div>
              </div>
            )}


            {/* Jelsz vltoztats */}
            <div className="password-section">
              <h3>Jelsz vltoztats</h3>
              <form onSubmit={handlePasswordChange}>
                <div className="form-grid">
                  <div className="form-group">
                    <label>Jelenlegi jelsz</label>
                    <input
                      type="password"
                      name="currentPassword"
                      value={userData.currentPassword}
                      onChange={handleInputChange}
                    />
                  </div>
                  <div className="form-group">
                    <label>j jelsz</label>
                    <input
                      type="password"
                      name="newPassword"
                      value={userData.newPassword}
                      onChange={handleInputChange}
                    />

                    {/* Itt jelenik meg az erssgjelz, ha mr elkezdtek gpelni */}
                    {userData.newPassword && (
                      <div className="password-strength-container">
                        <div style={{ background: "#eee", height: "6px", borderRadius: "4px", marginTop: "6px", width: "100%" }}>
                          <div style={{
                            width: `${passwordStrength.score * 20}%`,
                            height: "100%",
                            background: passwordStrength.color,
                            borderRadius: "4px",
                            transition: "0.3s"
                          }} />
                        </div>
                        <small style={{ color: passwordStrength.color, display: "block", marginTop: "4px" }}>
                          {passwordStrength.label}
                        </small>
                      </div>
                    )}
                  </div>
                  <div className="form-group">
                    <label>j jelsz megerstse</label>
                    <input
                      type="password"
                      name="confirmPassword"
                      value={userData.confirmPassword}
                      onChange={handleInputChange}
                    />
                    {passwordsDoNotMatch && (
                      <small style={{ color: "#ff4d4d", display: "block", marginTop: "6px" }}>
                        Az j jelsz s a megersts nem egyezik.
                      </small>
                    )}
                  </div>
                </div>
                {statusMessage.text && (
                  <div style={{
                    color: statusMessage.isError ? "#ff4d4d" : "#2ecc71",
                    textAlign: "center",
                    padding: "10px",
                    marginTop: "10px",
                    fontSize: "14px",
                    fontWeight: "bold",
                    backgroundColor: statusMessage.isError ? "#ffe6e6" : "#e6fffa", // Halvny httr a jobb olvashatsgrt
                    borderRadius: "4px"
                  }}>
                    {statusMessage.text}
                  </div>
                )}
                <div className="form-actions">
                  <button type="submit" className="btn-save">
                    Jelsz vltoztatsa
                  </button>
                </div>
              </form>
            </div>
          </section>

          {/* 2. Szlltsi cmek szakasz */}
          <section id="shipping" className="profile-section">
            <div className="section-header">
              <h2>Szlltsi cm</h2>
              {!shippingAddresses.length && !newAddressType && (
                <button
                  className="btn-add"
                  onClick={() => {
                    setNewAddress({ type: 'shipping', postalCode: '', city: '', address: '', phoneNumber: '' });
                    setNewAddressType('shipping');
                  }}
                >
                  + j cm
                </button>
              )}
            </div>

            {shippingAddresses.length > 0 ? (
              <div className="addresses-grid">
                {shippingAddresses.map((address, idx) => (
                  <div key={idx} className="address-card">
                    {editingShippingId === idx ? (
                      <form onSubmit={handleShippingSubmit}>
                        <div className="form-group">
                          <label>Irnytszm</label>
                          <input
                            type="text"
                            value={address.postalCode || ''}
                            onChange={(e) => handleAddressInputChange(e, 'shipping', idx, 'postalCode')}
                          />
                        </div>
                        <div className="form-group">
                          <label>Vros</label>
                          <input
                            type="text"
                            value={address.city || ''}
                            onChange={(e) => handleAddressInputChange(e, 'shipping', idx, 'city')}
                          />
                        </div>
                        <div className="form-group">
                          <label>Utca, hzszm</label>
                          <input
                            type="text"
                            value={address.address || ''}
                            onChange={(e) => handleAddressInputChange(e, 'shipping', idx, 'address')}
                          />
                        </div>
                        <div className="form-group">
                          <label>Telefonszm</label>
                          <input
                            type="text"
                            value={address.phoneNumber || ''}
                            onChange={(e) => handleAddressInputChange(e, 'shipping', idx, 'phoneNumber')}
                          />
                        </div>
                        <div className="address-actions">
                          <button type="submit" className="btn-save">Ments</button>
                          <button
                            type="button"
                            className="btn-cancel"
                            onClick={() => setEditingShippingId(null)}
                          >
                            Mgse
                          </button>
                        </div>
                      </form>
                    ) : (
                      <>
                        <div className="address-details">
                          <p><strong>Irnytszm:</strong> {address.postalCode}</p>
                          <p><strong>Vros:</strong> {address.city}</p>
                          <p><strong>Cm:</strong> {address.address}</p>
                          <p><strong>Telefonszm:</strong> {address.phoneNumber}</p>
                        </div>
                        <div className="address-actions">
                          <button
                            className="btn-edit"
                            onClick={() => setEditingShippingId(idx)}
                          >
                            Szerkeszts
                          </button>
                          <button
                            className="btn-delete"
                            onClick={() => deleteAddress('shipping', null)}
                          >
                            Trls
                          </button>
                        </div>
                      </>
                    )}
                  </div>
                ))}
              </div>
            ) : (
              <p className="text-muted">Nincs szlltsi cm megadva.</p>
            )}

            {addressStatusMessage.text && (
              <div style={{
                color: addressStatusMessage.isError ? "#ff4d4d" : "#2ecc71",
                textAlign: "center",
                padding: "10px",
                marginTop: "10px",
                fontSize: "14px",
                fontWeight: "bold",
                backgroundColor: addressStatusMessage.isError ? "#ffe6e6" : "#e6fffa",
                borderRadius: "4px"
              }}>
                {addressStatusMessage.text}
              </div>
            )}

            {newAddressType === 'shipping' && (
              <div className="address-card" style={{ marginTop: '20px' }}>
                <h4>j szlltsi cm</h4>
                <form onSubmit={handleAddNewAddress}>
                  <div className="form-group">
                    <label>Irnytszm</label>
                    <input
                      type="text"
                      value={newAddress.postalCode}
                      onChange={(e) => setNewAddress({ ...newAddress, postalCode: e.target.value })}
                      placeholder="1052"
                      required
                    />
                  </div>
                  <div className="form-group">
                    <label>Vros</label>
                    <input
                      type="text"
                      value={newAddress.city}
                      onChange={(e) => setNewAddress({ ...newAddress, city: e.target.value })}
                      placeholder="Budapest"
                      required
                    />
                  </div>
                  <div className="form-group">
                    <label>Utca, hzszm</label>
                    <input
                      type="text"
                      value={newAddress.address}
                      onChange={(e) => setNewAddress({ ...newAddress, address: e.target.value })}
                      placeholder="Kossuth Lajos utca 12."
                      required
                    />
                  </div>
                  <div className="form-group">
                    <label>Telefonszm</label>
                    <input
                      type="text"
                      value={newAddress.phoneNumber}
                      onChange={(e) => setNewAddress({ ...newAddress, phoneNumber: e.target.value })}
                      placeholder="+36 30 123 4567"
                      required
                    />
                  </div>
                  <div className="address-actions">
                    <button type="submit" className="btn-save">
                      Cm hozzadsa
                    </button>
                    <button
                      type="button"
                      className="btn-cancel"
                      onClick={() => {
                        setNewAddressType(null);
                        setNewAddress({ type: 'shipping', postalCode: '', city: '', address: '', phoneNumber: '' });
                      }}
                    >
                      Mgse
                    </button>
                  </div>
                </form>
              </div>
            )}
          </section>
          <section id="billing" className="profile-section">
            <div className="section-header">
              <h2>Szmlzsi cm</h2>
              {!billingAddresses.length && !newAddressType && (
                <button
                  className="btn-add"
                  onClick={() => {
                    setNewAddress({ type: 'billing', postalCode: '', city: '', address: '', phoneNumber: '' });
                    setNewAddressType('billing');
                  }}
                >
                  + j cm
                </button>
              )}
            </div>

            {billingAddresses.length > 0 ? (
              <div className="addresses-grid">
                {billingAddresses.map((address, idx) => (
                  <div key={idx} className="address-card">
                    {editingBillingId === idx ? (
                      <form onSubmit={handleBillingSubmit}>
                        <div className="form-group">
                          <label>Irnytszm</label>
                          <input
                            type="text"
                            value={address.postalCode || ''}
                            onChange={(e) => handleAddressInputChange(e, 'billing', idx, 'postalCode')}
                          />
                        </div>
                        <div className="form-group">
                          <label>Vros</label>
                          <input
                            type="text"
                            value={address.city || ''}
                            onChange={(e) => handleAddressInputChange(e, 'billing', idx, 'city')}
                          />
                        </div>
                        <div className="form-group">
                          <label>Utca, hzszm</label>
                          <input
                            type="text"
                            value={address.address || ''}
                            onChange={(e) => handleAddressInputChange(e, 'billing', idx, 'address')}
                          />
                        </div>
                        <div className="form-group">
                          <label>Telefonszm</label>
                          <input
                            type="text"
                            value={address.phoneNumber || ''}
                            onChange={(e) => handleAddressInputChange(e, 'billing', idx, 'phoneNumber')}
                          />
                        </div>
                        <div className="address-actions">
                          <button type="submit" className="btn-save">Ments</button>
                          <button
                            type="button"
                            className="btn-cancel"
                            onClick={() => setEditingBillingId(null)}
                          >
                            Mgse
                          </button>
                        </div>
                      </form>
                    ) : (
                      <>
                        <div className="address-details">
                          <p><strong>Irnytszm:</strong> {address.postalCode}</p>
                          <p><strong>Vros:</strong> {address.city}</p>
                          <p><strong>Cm:</strong> {address.address}</p>
                          <p><strong>Telefonszm:</strong> {address.phoneNumber}</p>
                        </div>
                        <div className="address-actions">
                          <button
                            className="btn-edit"
                            onClick={() => setEditingBillingId(idx)}
                          >
                            Szerkeszts
                          </button>
                          <button
                            className="btn-delete"
                            onClick={() => deleteAddress('billing', null)}
                          >
                            Trls
                          </button>
                        </div>
                      </>
                    )}
                  </div>
                ))}
              </div>
            ) : (
              <p className="text-muted">Nincs szmlzsi cm megadva.</p>
            )}

            {addressStatusMessage.text && (
              <div style={{
                color: addressStatusMessage.isError ? "#ff4d4d" : "#2ecc71",
                textAlign: "center",
                padding: "10px",
                marginTop: "10px",
                fontSize: "14px",
                fontWeight: "bold",
                backgroundColor: addressStatusMessage.isError ? "#ffe6e6" : "#e6fffa",
                borderRadius: "4px"
              }}>
                {addressStatusMessage.text}
              </div>
            )}

            {newAddressType === 'billing' && (
              <div className="address-card" style={{ marginTop: '20px' }}>
                <h4>j szmlzsi cm</h4>
                <form onSubmit={handleAddNewAddress}>
                  <div className="form-group">
                    <label>Irnytszm</label>
                    <input
                      type="text"
                      value={newAddress.postalCode}
                      onChange={(e) => setNewAddress({ ...newAddress, postalCode: e.target.value })}
                      placeholder="1052"
                      required
                    />
                  </div>
                  <div className="form-group">
                    <label>Vros</label>
                    <input
                      type="text"
                      value={newAddress.city}
                      onChange={(e) => setNewAddress({ ...newAddress, city: e.target.value })}
                      placeholder="Budapest"
                      required
                    />
                  </div>
                  <div className="form-group">
                    <label>Utca, hzszm</label>
                    <input
                      type="text"
                      value={newAddress.address}
                      onChange={(e) => setNewAddress({ ...newAddress, address: e.target.value })}
                      placeholder="Kossuth Lajos utca 12."
                      required
                    />
                  </div>
                  <div className="form-group">
                    <label>Telefonszm</label>
                    <input
                      type="text"
                      value={newAddress.phoneNumber}
                      onChange={(e) => setNewAddress({ ...newAddress, phoneNumber: e.target.value })}
                      placeholder="+36 30 123 4567"
                      required
                    />
                  </div>
                  <div className="address-actions">
                    <button type="submit" className="btn-save">
                      Cm hozzadsa
                    </button>
                    <button
                      type="button"
                      className="btn-cancel"
                      onClick={() => {
                        setNewAddressType(null);
                        setNewAddress({ type: 'billing', postalCode: '', city: '', address: '', phoneNumber: '' });
                      }}
                    >
                      Mgse
                    </button>
                  </div>
                </form>
              </div>
            )}
          </section>
          <section id="orders" className="profile-section">
            <div className="section-header">
              <h2>Megrendelsek</h2>
            </div>

            <div className="orders-grid">
              {phones.map((phone, index) => (
                <div key={index} className="order-card">
                  <div className="order-header">
                    <h3>{phone.phoneName}</h3>
                    <span className={`order-status status-${getStatusText(phone.status).replace(/\s/g, '-')}`}>
                      {getStatusText(phone.status)}
                    </span>
                  </div>

                  <div className="order-details">
                    <p><strong>Darabszm:</strong> {phone.amount} db</p>
                    <p><strong>r:</strong> {phone.price.toLocaleString()} Ft</p>
                    <p><strong>RAM/Trhely:</strong> {phone.phoneRam}/{phone.phoneStorage}</p>
                    <p><strong>Szn:</strong> {phone.phoneColorName}</p>
                  </div>
                </div>
              ))}
            </div>
          </section>



          {/*Szerviz krsek */}
          <section id="service-requests" className="profile-section">
            <div className="section-header">
              <h2>Szervizeltetsek</h2>
            </div>

            <div className="orders-grid">
              {serviceRequests.length > 0 ? (
                serviceRequests.map((request, index) => (
                  <div key={index} className="service-request-card">
                    <div className="service-request-header">
                      <h3>{request.phoneName}</h3>
                      <span className={`service-request-status status-${getStatusText(request.status).replace(/\s/g, '-')}`}>
                        {getStatusText(request.status)}
                      </span>
                    </div>
                    <div className="service-request-details">
                      <p><strong>Gyrt:</strong> {request.manufacturerName}</p>
                      <p><strong>Irnytszm:</strong> {request.postalCode}</p>
                      <p><strong>Vros:</strong> {request.city}</p>
                      <p><strong>Cm:</strong> {request.address}</p>
                      <p><strong>Telefonszm:</strong> {request.phoneNumber}</p>
                      <p><strong>Hiba lersa:</strong> {request.problemDescription}</p>
                      <p><strong>Vizsglat:</strong> {request.phoneInspection ? "rdekelne" : "nem rdekelne"}</p>
                      <p><strong>r:</strong> {request.price ? request.price.toLocaleString() + ' Ft' : 'rajnlat fggben'}</p>
                    </div>
                  </div>
                ))
              ) : (
                <p className="text-muted">Nincs szervizeltetsi krs.</p>
              )}
            </div>
          </section>

          {/* Cm trls megerst modal */}
          {showDeleteModal && (
            <div className="profile-modal-overlay">
              <div className="profile-modal-content">
                <div className="profile-modal-header">
                  <h3 className="profile-modal-title">Megersts</h3>
                </div>
                <div className="profile-modal-body">
                  <p className="profile-modal-text">Biztosan szeretnd trlni ezt a cmet?</p>
                </div>
                <div className="profile-modal-actions">
                  <button 
                    onClick={() => {
                      setShowDeleteModal(false);
                      setDeleteAddressType(null);
                      setDeleteAddressId(null);
                    }} 
                    className="profile-modal-btn-cancel"
                  >
                    Mgse
                  </button>
                  <button 
                    onClick={confirmDeleteAddress}
                    className="profile-modal-btn-confirm"
                  >
                    Igen, trls
                  </button>
                </div>
              </div>
            </div>
          )}

        </main>
      </div>
    </div>
  );
};

export default Profile;