import React from 'react';
import { Link } from 'react-router-dom';
import './InfoPages.css';

export default function Contact() {
  return (
    <div className="container info-page">
      <section className="info-hero">
        <h1>Kapcsolat</h1>
        <p>
          Ez az oldal akkor segt, ha elakadtl a folyamatban, s nem tudod, merre menj tovbb.
        </p>
      </section>

      <section className="info-section row g-4">
        <div className="col-lg-6">
          <div className="info-card h-100">
            <span className="info-badge">Ajnlott tvonal</span>
            <h5>Ha kszlkhibd van</h5>
            <ol className="info-steps">
              <li>Nyisd meg a <strong>Szerviz</strong> oldalt, s nzd t az opcikat.</li>
              <li>Hasznld a chatbotot, ha nem egyrtelm a hiba.</li>
              <li>Indtsd el a <strong>Szerviz ignylst</strong>, s kvesd a kapott instrukcikat.</li>
            </ol>
            <div className="info-note mt-3">
              A rendszer elsdlegesen az oldalon belli lpsekre pl, nem kln kapcsolatfelvtelre.
            </div>
            <Link to="/szerviz" className="btn info-btn">Szerviz megnyitsa</Link>
          </div>
        </div>
        <div className="col-lg-6">
          <div className="info-card h-100">
            <span className="info-badge">Ha teljesen elvesztl</span>
            <h5>Vgs tmpont</h5>
            <p>
              Ha mr nem tudod, melyik oldalon kell folytatnod, innen indulj:
            </p>
            <ul className="info-list">
              <li><strong>Bolt:</strong> telefon keress, szrs, sszehasonlts</li>
              <li><strong>Szerviz:</strong> hiba tpusok s javtsi lehetsgek</li>
              <li><strong>Szerviz ignyls:</strong> konkrt bekldsi folyamat</li>
            </ul>
            <div className="info-actions">
              <Link to="/bolt" className="btn info-btn-secondary">Bolt</Link>
              <Link to="/szervizigenyles" className="btn info-btn">Szerviz ignylse</Link>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
