import React, { useState } from 'react';
import { useSearchParams, Link, useNavigate } from 'react-router-dom'; // Importld a useNavigate-et
import './ResigstrationConfirm.css';
import axios from 'axios';

export default function RegistrationConfirm() {
    const [searchParams] = useSearchParams();
    const navigate = useNavigate(); // Inicializld a navigcit
    const [message, setMessage] = useState("");
    const [isSuccess, setIsSuccess] = useState(false);

    const name = searchParams.get('name') || 'Felhasznl';
    const email = searchParams.get('email');

    const handleActivation = async (e) => {
        e.preventDefault();
        
        try {
            const response = await axios.put(`http://localhost:5175/api/Registration?name=${name}&email=${email}`);

            if(response.status === 200) {
                setMessage("Sikeres aktivls! Most mr bejelentkezhetsz. tirnyts...");
                setIsSuccess(true);

                setTimeout(() => {
                    navigate('/bejelentkezes');
                }, 2000);

            } else {
                setMessage("Hiba trtnt az aktivls sorn. Krjk, prbld jra ksbb.");
                setIsSuccess(false);
            }
        } catch (error) {
            setMessage("A szerver nem rhet el.");
            setIsSuccess(false);
        }
    };

    return (
        <div className="container_RegistrationConfirm">
            <div className="card_RegistrationConfirm">
                
                <div className="backLink_RegistrationConfirm">
                    <Link to="/"><i className="fa-solid fa-arrow-left"></i> Vissza az oldalra</Link>
                </div>

                <h2 className="title_RegistrationConfirm">Fik aktivlsa</h2>

                <p className="infoText_RegistrationConfirm">
                    Kedves <strong>{name}</strong>! <br />
                    Krjk, erstsd meg a regisztrcidat az albbi gombra kattintva.
                </p>
                
                <p className="emailDisplay_RegistrationConfirm">
                    Regisztrlt e-mail cm: {email}
                </p>

                <form onSubmit={handleActivation}>
                    <button type='submit' className="submitButton_RegistrationConfirm">
                        Fik aktivlsa
                    </button>
                </form>

                <p className="alert_RegistrationConfirm" style={{ color: isSuccess ? 'green' : 'red' }}>
                    {message}
                </p>

                <Link to="/bejelentkezes" className="switchLink_RegistrationConfirm">
                    Mr aktivltad? Jelentkezz be itt!
                </Link>
            </div>
        </div>
    );
}
