import React, { useEffect, useState } from 'react';
import '../../node_modules/bootstrap/dist/css/bootstrap.css';
import './Home.css';
import Navbar from '../components/Navbar'; //we still need the import here
import PhoneCard from '../components/PhoneCard';
import InputText from '../components/InputText';
import { Link } from 'react-router';
import axios from 'axios';

export default function Home() {
  const allPhonesURL = "http://localhost:5175/mainPage";

  const [currentPage, setCurrentPage] = useState(0);
  const [activeChangeButton, setActiveChangeButton] = useState("right");



  const [allPhonesData, setAllPhonesData] = useState([]);

  useEffect(() => {
    axios.get(allPhonesURL)
      .then((response) => {
        setAllPhonesData(response.data);
      })
      .catch((error) => console.error("Hiba a JSON betltsekor:", error));
  }, []);


  function changePage(direction) {
    const newPage = currentPage + direction;
    setCurrentPage(newPage);

    if (newPage === 1) {
      setActiveChangeButton("left");
    } else if (newPage === 0) {
      setActiveChangeButton("right");
    }
  }


  function displayPhoneCards() {
    let start = 0;
    activeChangeButton === "right" ? start = 0 : start = 4;
    return allPhonesData.slice(start, start + 4).map((phone) => (
      <PhoneCard
        key={phone.phoneID}
        phoneId={phone.phoneID}
        phoneName={phone.phoneName}
        phoneInStore={(phone.phoneInStore === 1 ? "Raktron" : "Nincs raktron")}
        phonePrice={phone.phonePrice}
        colors={phone.colors || []}
        ramStoragePairs={phone.ramStoragePairs || []}
      />
    ))
  }

  return (
    <div>
      <br />
      <br />
      <div className="row">
        <div className="col-1">
          {
            activeChangeButton === "left" ? <button onClick={() => changePage(-1)} id="carouselButtonLeft" className="carouselButton">
              <i className="fa-solid fa-arrow-left"></i>
            </button> : ""
          }

        </div>
        <div className='col-10'>
          <div id='contentRow'>
            {allPhonesData.length === 0 ? (
              <p>Telefonok betltse...</p>
            ) : (
              displayPhoneCards()
            )}
          </div>
        </div>
        <div className="col-1">
          {
            activeChangeButton === "right" ? <button onClick={() => changePage(1)} id="carouselButtonRight" className="carouselButton">
              <i className="fa-solid fa-arrow-right"></i>
            </button> : ""
          }
        </div>
        <div className="dots">
          <div className={`dot ${currentPage === 0 ? "dotActive" : ""}`} id="leftDot"></div>
          <div className={`dot ${currentPage === 1 ? "dotActive" : ""}`} id="rightDot"></div>
        </div>
        <div className="hr">
          <hr size="5" />
        </div>

      </div>
      <div className="container service-section ">
        <div className="row align-items-center">
          <div className="col-lg-6 service-text">
            <h2>Gyors s Megbzhat Szerviz</h2>
            <p>
              Meghibsodott a telefonod? Ne aggdj! Nlunk gyors s szakszer javtst kapsz,
              hogy minl hamarabb jra hasznlhasd a kszlked. Tapasztalt szakembereink s
              prmium minsg alkatrszeink garantljk a hossz tv megbzhatsgot.
            </p>
            <p>
              Mirt vlassz minket?</p>
            <ul>
              <li>Gyors javts, akr nhny rn bell</li>
              <li>Kedvez rak s tlthat kltsgek</li>
              <li>Eredeti vagy prmium minsg alkatrszek</li>
              <li>Garancia minden javtsra</li>
            </ul>

            <Link className="btn btn-primary service-btn"
              to="/szervizigenyles">Irny a <strong>szervz</strong>
              oldal</Link>
          </div>
          <div className="col-lg-6 service-image">
            <img loading="lazy" src="../Images/telefonservice.png" alt="Telefon Szervz" className="rounded-image" />
          </div>
        </div>
      </div>
    </div>

  );
}
