import React from 'react'
import './Service.css';
import { Link } from 'react-router';

export default function Service() {
  return (
    <div>
      <div className="container service-section">
        <div className="row align-items-center">
          {/* BAL OLDAL  SZVEG */}
          <div className="col-lg-6 service-text left-side">
            <h2>Gyors s Megbzhat Szerviz</h2>

            <p>
              Meghibsodott a telefonod? Ne aggdj! Nlunk gyors s szakszer
              javtst kapsz, hogy minl hamarabb jra hasznlhasd a kszlked.
              Tapasztalt szakembereink s prmium minsg alkatrszeink
              garantljk a hossz tv megbzhatsgot.
            </p>

            <p>Mirt vlassz minket?</p>

            <ul id="serviceList">
              <li
                data-bs-target="#serviceCarousel"
                data-bs-slide-to="0"
                style={{ cursor: "pointer" }}
              >
                Gyors javts, akr nhny rn bell
              </li>
              <li
                data-bs-target="#serviceCarousel"
                data-bs-slide-to="1"
                style={{ cursor: "pointer" }}
              >
                Kedvez rak s tlthat kltsgek
              </li>
              <li
                data-bs-target="#serviceCarousel"
                data-bs-slide-to="2"
                style={{ cursor: "pointer" }}
              >
                Eredeti vagy prmium minsg alkatrszek
              </li>
              <li
                data-bs-target="#serviceCarousel"
                data-bs-slide-to="3"
                style={{ cursor: "pointer" }}
              >
                Garancia minden javtsra
              </li>
              <li
                data-bs-target="#serviceCarousel"
                data-bs-slide-to="4"
                style={{ cursor: "pointer" }}
              >
                Dertsd ki mi baja a telefonodnak
              </li>
            </ul>
          </div>

          {/* JOBB OLDAL  CAROUSEL */}
          <div className="col-lg-6 service-text">
            <div
              id="serviceCarousel"
              className="carousel slide"
              data-bs-ride="carousel"
            >
              <div className="carousel-inner">
                <div className="carousel-item active">
                  <div className="service-details-title" style={{ color: "#2300B3" }}>
                    Gyors javts
                  </div>
                  <div className="service-details-text">
                    Szakembereink a legtbb hibt akr nhny rn bell elhrtjk,
                    hogy ne maradj sokig telefon nlkl.
                  </div>
                  <img
                    src="/Images/telefonservice.png"
                    alt="Gyors javts"
                    className="carousel-img mt-3"
                  />
                </div>

                <div className="carousel-item">
                  <div className="service-details-title" style={{ color: "#68F145" }}>
                    Kedvez rak s tlthat kltsgek
                  </div>
                  <div className="service-details-text">
                    Minden javts eltt pontos rajnlatot kapsz, rejtett kltsgek
                    nlkl.
                  </div>
                  <img
                    src="/Images/service2.png"
                    alt="Kedvez rak"
                    className="carousel-img mt-3"
                  />
                </div>

                <div className="carousel-item">
                  <div className="service-details-title" style={{ color: "#3E9696" }}>
                    Eredeti vagy prmium minsg alkatrszek
                  </div>
                  <div className="service-details-text">
                    Csak eredeti vagy prmium minsg alkatrszeket hasznlunk.
                  </div>
                  <img
                    src="/Images/service3.png"
                    alt="Minsgi alkatrszek"
                    className="carousel-img mt-3"
                  />
                </div>

                <div className="carousel-item">
                  <div className="service-details-title" style={{ color: "#FF4742" }}>
                    Garancia minden javtsra
                  </div>
                  <div className="service-details-text">
                    Minden elvgzett munkra garancit adunk.
                  </div>
                  <img
                    src="/Images/service4.png"
                    alt="Garancia"
                    className="carousel-img mt-3"
                  />
                </div>

                <div className="carousel-item">
                  <div className="service-details-title" style={{ color: "#9ccd8c" }}>
                    Dertsd ki mi baja a telefonodnak
                  </div>
                  <div className="service-details-text">
                    Hasznld a chatbotunkat, s pr krds utn segtnk!
                  </div>
                  <img
                    src="/Images/service5.png"
                    alt="Chatbot"
                    className="carousel-img mt-3"
                  />
                </div>
              </div>

              <button
                className="carousel-control-prev"
                type="button"
                data-bs-target="#serviceCarousel"
                data-bs-slide="prev"
              >
                <span className="carousel-control-prev-icon" />
              </button>

              <button
                className="carousel-control-next"
                type="button"
                data-bs-target="#serviceCarousel"
                data-bs-slide="next"
              >
                <span className="carousel-control-next-icon" />
              </button>
            </div>
          </div>

          {/* GOMBOK */}
          <button className="btn btn-outline-info mt-4 w-100" id="chatbotBtn">
            <i className="fas fa-robot"></i> Chatbot: Segtsg a hibhoz
          </button>

          <Link className="btn btn-primary service-btn"
            to="/szervizigenyles"><strong>Szervz ignylse</strong>
          </Link>
        </div>

        <div className="hr">
          <hr size="5" />
        </div>


      </div>
    </div>
  );
};
