import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';


export default function ServiceRequest() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    device: '',
    problem: '',
    atvizsgalas: false
  });
  const [selectedProblems, setSelectedProblems] = useState([]);
  const [formMessage, setFormMessage] = useState('');
  const [showAtvizsgalasInfo, setShowAtvizsgalasInfo] = useState(false);
  const [uniqueCode, setUniqueCode] = useState('');
  const navigate = useNavigate();

  const problemOptions = [
    { id: 'problemDisplay', value: 'kijelzo', label: 'Kijelz' },
    { id: 'problemBattery', value: 'akku', label: 'Akkumultor' },
    { id: 'problemSoftware', value: 'szoftver', label: 'Szoftver' },
    { id: 'problemCamera', value: 'kamera', label: 'Kamera' },
    { id: 'problemSound', value: 'hang', label: 'Hang' },
    { id: 'problemOther', value: 'egyeb', label: 'Egyb' }
  ];

  useEffect(() => {
    generateUniqueCode();
  }, []);

  const generateUniqueCode = () => {
    const now = new Date();
    const datePart = now.getFullYear().toString().slice(-2) +
      (now.getMonth() + 1).toString().padStart(2, '0') +
      now.getDate().toString().padStart(2, '0') +
      now.getHours().toString().padStart(2, '0') +
      now.getMinutes().toString().padStart(2, '0');
    const randomPart = Math.floor(1000 + Math.random() * 9000);
    const code = `PHNSCT-${datePart}-${randomPart}`;
    setUniqueCode(code);
    return code;
  };

  const handleInputChange = (e) => {
    const { id, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [id]: type === 'checkbox' ? checked : value
    }));

    if (id === 'atvizsgalas') {
      setShowAtvizsgalasInfo(checked);
    }
  };

  const handleProblemToggle = (problem) => {
    setSelectedProblems(prev => {
      if (prev.includes(problem.value)) {
        return prev.filter(p => p !== problem.value);
      } else {
        return [...prev, problem.value];
      }
    });
  };

  const getSelectedProblemLabels = () => {
    return problemOptions
      .filter(problem => selectedProblems.includes(problem.value))
      .map(problem => problem.label);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // Validate required fields
    if (!formData.name || !formData.email || !formData.phone || !formData.device || !formData.problem) {
      setFormMessage(
        <div className="alert alert-danger">
          Krjk, tltse ki az sszes ktelez mezt!
        </div>
      );
      return;
    }

    const code = generateUniqueCode();
    const feltetelLink = `<a href="/csomagolasfeltetelek" target="_blank" style="color:#17a2b8; text-decoration:underline;">ITT</a>`;
    const selectedLabels = getSelectedProblemLabels().join(', ');

    if (formData.atvizsgalas) {
      setFormMessage(
        <div className="alert alert-info">
          Krst megkaptuk, s rgztettk.<br />
          <b>n <strong>Telefon tvizsglst</strong> krt.</b><br />
          {selectedLabels && <><b>Kivlasztott problmk:</b> {selectedLabels}<br /></>}
          <strong>Krjk, kldje el telefonjt postn a kvetkez cmre, vagy hozza be zletnkbe:</strong><br />
          <b>Cm:</b> Miskolc, Palczy Lszl utca 3, 3525<br />
          <b>Nv:</b> PhoneScout Szerviz<br />
          <b>Telefonszm:</b> +36 30 123 4567<br />
          <b>Email:</b> info@phonescout.hu<br />
          <br />
          <b>Bevizsglsi kdja:</b> <span style={{ fontSize: '1.2em', color: '#17a2b8' }}><strong>{code}</strong></span><br />
          <b>Krjk, a csomagba mellkelje:</b><br />
          <ul>
            <li>A nevet amit itt megadott,</li>
            <li>A telefonszmot amit itt megadott,</li>
            <li>A kszlket a csomagolsi feltteleknek megfelelen becsomagolva <Link to="/csomagolasfeltetelek" target="_blank" style={{ color: '#17a2b8', textDecoration: 'underline' }}>ITT</Link>,</li>
            <li>A bevizsglsi kdjt. Az n kdja: <strong>{code}</strong></li>
          </ul>
          <br />
        </div>
      );
    } else {
      setFormMessage(
        <div className="alert alert-info">
          Krst megkaptuk, s rgztettk.<br />
          {selectedLabels && <><b>Kivlasztott problmk:</b> {selectedLabels}<br /></>}
          <strong>Krjk, kldje el telefonjt postn a kvetkez cmre, vagy hozza be zletnkbe:</strong><br />
          <b>Cm:</b> Miskolc, Palczy Lszl utca 3, 3525<br />
          <b>Nv:</b> PhoneScout Szerviz<br />
          <b>Telefonszm:</b> +36 30 123 4567<br />
          <b>Email:</b> info@phonescout.hu<br />
          <br />
          <b>Bevizsglsi kdja:</b> <span style={{ fontSize: '1.2em', color: '#17a2b8' }}><strong>{code}</strong></span><br />
          <b>Krjk, a csomagba mellkelje:</b><br />
          <ul>
            <li>A nevet amit itt megadott,</li>
            <li>A telefonszmot amit itt megadott,</li>
            <li>A kszlket a csomagolsi feltteleknek megfelelen becsomagolva <Link to="/csomagolasfeltetelek" target="_blank" style={{ color: '#17a2b8', textDecoration: 'underline' }}>ITT</Link>,</li>
            <li>A bevizsglsi kdjt. Az n kdja: <strong>{code}</strong></li>
          </ul>
          <b>Ha szemlyesen jn be:</b><br />
          <ul>
            <li>Hozza magval az E-mailt amit tlnk kapott</li>
            <br />
            <p><strong>VAGY</strong></p>
            <li>Bevizsglsi kdjt: <strong>{code}</strong></li>
            <li>Valamilyen elrhetsgt amit megadott az rlapon (pl. email, telefonszm)  </li>

          </ul>
          <br />
        </div>
      );
    }

    setFormData({
      name: '',
      email: '',
      phone: '',
      device: '',
      problem: '',
      atvizsgalas: false
    });
    setSelectedProblems([]);
    setShowAtvizsgalasInfo(false);
  };

  const openChatbot = () => {
    alert("Chatbot nyitsa...");
  };

  const faqItems = [
    {
      id: 'faq1',
      question: 'Mennyi id alatt kszl el a szervizels?',
      answer: 'ltalban 3-5 munkanap, de a pontos idt a hiba tpusa s az alkatrsz elrhetsge befolysolja.'
    },
    {
      id: 'faq2',
      question: 'Kapok rajnlatot a javts eltt?',
      answer: 'Igen, minden esetben elzetes rajnlatot kldnk az tvizsgls utn.'
    },
    {
      id: 'faq3',
      question: 'Hogyan kvethetem nyomon a szerviz sttuszt?',
      answer: 'A bevizsglsi kd segtsgvel az online szerviz sttusz lekrdez oldalon.'
    }
  ];

  return (
    <div>

      <div className="container mt-3 mb-2 text-end">
        <button id="chatbotBtn" className="btn btn-outline-info" onClick={openChatbot}>
          <i className="fas fa-robot"></i> Chatbot: Segtsg a hibhoz
        </button>
      </div>

      <div className="container my-5">
        <h2>Szervizels ignylse</h2>
        <form id="szervizForm" className="row g-3" onSubmit={handleSubmit}>
          <div className="col-md-6">
            <label htmlFor="name" className="form-label">Nv</label>
            <input
              type="text"
              className="form-control"
              id="name"
              value={formData.name}
              onChange={handleInputChange}
              required
            />
          </div>
          <div className="col-md-6">
            <label htmlFor="email" className="form-label">Email cm</label>
            <input
              type="email"
              className="form-control"
              id="email"
              value={formData.email}
              onChange={handleInputChange}
              required
            />
          </div>
          <div className="col-md-6">
            <label htmlFor="phone" className="form-label">Telefonszm</label>
            <input
              type="tel"
              className="form-control"
              id="phone"
              value={formData.phone}
              onChange={handleInputChange}
              required
              maxLength="11"
            />
          </div>
          <div className="col-md-6">
            <label htmlFor="device" className="form-label">Kszlk gyrtja</label>
            <input
              type="text"
              className="form-control"
              id="device"
              value={formData.device}
              onChange={handleInputChange}
              required
            />
          </div>
          <div className="col-md-6">
            <label className="form-label">Mivel van baj?</label>
            <div className="dropdown">
              <button
                className="btn btn-outline-secondary dropdown-toggle w-100 text-start"
                type="button"
                id="problemDropdown"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                {getSelectedProblemLabels().length > 0 ? getSelectedProblemLabels().join(', ') : 'Vlassz problmkat...'}
              </button>
              <div className="dropdown-menu p-3" aria-labelledby="problemDropdown" style={{ minWidth: '250px' }}>
                {problemOptions.map(problem => (
                  <div className="form-check" key={problem.id}>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id={problem.id}
                      checked={selectedProblems.includes(problem.value)}
                      onChange={() => handleProblemToggle(problem)}
                    />
                    <label className="form-check-label" htmlFor={problem.id}>
                      {problem.label}
                    </label>
                  </div>
                ))}
              </div>
            </div>
          </div>
          <div className="col-md-6 d-flex align-items-end">
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                id="atvizsgalas"
                checked={formData.atvizsgalas}
                onChange={handleInputChange}
              />
              <label className="form-check-label" htmlFor="atvizsgalas">
                tvizsgls krse
              </label>
            </div>
          </div>

          {showAtvizsgalasInfo && (
            <div className="col-12" id="atvizsgalasInfo">
              <div className="alert alert-secondary mt-2">
                Az tvizsgls sorn szakembereink teljes kren ellenrzik a kszlk llapott, feltrjk a
                rejtett hibkat, s javaslatot tesznek a javtsra vagy cserre. Az tvizsgls dja a vizsglat
                eredmnytl fggen kerl megllaptsra.
              </div>
            </div>
          )}

          <div className="col-12">
            <label htmlFor="problem" className="form-label">Hiba lersa</label>
            <textarea
              className="form-control"
              id="problem"
              rows="3"
              value={formData.problem}
              onChange={handleInputChange}
              required
            ></textarea>
          </div>
          <hr />
          <h3>Szmlzsi cm</h3>
          <div className="col-md-1">
            <label htmlFor="zipcode" className="form-label">Irnytszm</label>
            <input
              type="text"
              className="form-control"
              id="zipcode"
              value={formData.zipcode}
              onChange={handleInputChange}
              onInput={(e) => {
                e.target.value = e.target.value.replace(/\D/g, '').slice(0, 4);
              }}
              required
            />
          </div>
          <div className="col-md-3">
            <label htmlFor="city" className="form-label">Vros</label>
            <input
              type="text"
              className="form-control"
              id="city"
              value={formData.city}
              onChange={handleInputChange}
              required
            />
          </div>
          <div className="col-md-4">
            <label htmlFor="address" className="form-label">Cm (utca, hzszm)</label>
            <input
              type="text"
              className="form-control"
              id="address"
              value={formData.address}
              onChange={handleInputChange}
              required
            />
          </div>
          <div className="col-4"> </div>
          <div className="col-4">
            <button type="submit" className="btn btn-primary">Ignyls elkldse</button>
          </div>

          <div id="formMessage" className="mt-3">
            {formMessage}
          </div>
        </form>
      </div>

      <div className="hr">
        <hr size="5" />
      </div>

      <div className="container my-5">
        <h3>Gyakran Ismtelt Krdsek</h3>
        <div className="accordion" id="faqAccordion">
          {faqItems.map((faq, index) => (
            <div className="accordion-item" key={faq.id}>
              <h2 className="accordion-header" id={`heading${index}`}>
                <button
                  className="accordion-button collapsed"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target={`#collapse${index}`}
                >
                  {faq.question}
                </button>
              </h2>
              <div
                id={`collapse${index}`}
                className="accordion-collapse collapse"
                data-bs-parent="#faqAccordion"
              >
                <div className="accordion-body">
                  {faq.answer}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

    </div>
  );
}